export default{
	data(){
		return {
			pwd:'',
			username:'',
			islogin:false
		}
	},
	onShow() {
		this.islogin=global.isLogin()&&true
		if(this.islogin){
			this.username=global.isLogin().username
		}
	},
	methods:{
		loginV(){
			uni.navigateTo({
				url:'index/index'
			})
		},
		wxLogin(){
			uni.getProvider({
				service:'oauth',
				success(res) {
					if(res.provider.indexOf('weixin')){
						uni.login({
							provider:'weixin',
							success(Loginres) {
							}
						})
					}
				}
			})
		},
		backtoindex(){
			uni.navigateTo({
				url:'./index/index'
			})
		},
		getUserInfo(res){
			if(!res.detail.iv){
				uni.showToast({
					title:'未授权，登录失败',
					icon:'none'
				})
				return
			}
			uni.login({
				provider:'weixin',
				success(Loginres) {
					if(Loginres.code){
						var APPID='wx2a8b288dba2fad3c'
						var SECRET='2a6c99a14fd8d8044c2ff40ff5f94289'
						var JSCODE=Loginres.code
						uni.request({
							url:`https://api.weixin.qq.com/sns/jscode2session?appid=${APPID}&secret=${SECRET}&js_code=${JSCODE}&grant_type=authorization_code`,
							success(urlres) {
								uni.setStorage({
										key:'userinfo',
										data:{
											username:res.detail.userInfo.nickName,
											imageSrc:res.detail.userInfo.avatarUrl
										}
									})
									uni.navigateTo({
										url:'/pages/index/index'
									})
							}
						})
					}
				}
			})
			// else{
			// 	uni.setStorage({
			// 		key:'userinfo',
			// 		data:{
			// 			username:res.detail.userInfo.nickName,
			// 			imageSrc:res.detail.userInfo.avatarUrl
			// 		}
			// 	})
			// 	uni.navigateTo({
			// 		url:'/pages/index/index'
			// 	})
				
			// }

		},
		resetL(){
			uni.clearStorage()
			uni.reLaunch({
				url:'./audioplay'
			})
		},
		checkdata(){
			if(this.username.length<1){
				uni.showToast({
					icon:'none',
					title:'请输入用户名'
				})
				return
			}else if(this.pwd.length<1){
				uni.showToast({
					icon:'none',
					title:'请输入密码'
				})
				return
			}
			else if(this.username==='zy'&&this.pwd==='1234'){
				uni.setStorage({
					key:'userinfo',
					data:{
						username:this.username
					}
				})
				uni.navigateTo({
					url:'/pages/index/index'
				})
			}
			else{
				uni.showToast({
					icon:'none',
					title:'用户名或密码错误'
				})
			}
	}
},
}
